A,CAIJ,+BACE,YAAA,CACA,qBAAA,CACA,kBAAA,CAEA,2CACE,UAAA,CACA,WAAA,CACA,kBAAA,CACA,UAAA,CACA,iBAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,cAAA,CAGF,2CACE,cAAA,CACA,aAAA,CACA,cAAA,CAIJ,+BACE,cAAA,CACA,QAAA,CACA,QAAA,CACA,0BAAA,CACA,YAAA,CAEA,8CACE,YAAA,CACA,kBAAA,CACA,QAAA,CACA,iBAAA,CACA,kDAAA,CACA,kBAAA,CACA,0CAAA,CACA,UAAA,CAGF,2CACE,cAAA,CAGF,4CACE,cAAA,CACA,UAAA,CAGF,2CACE,cAAA,CACA,gBAAA,CAIJ,iDACE,iCAAA,CAGF,iDACE,kCAAA,CAGF,0BACE,GACE,SAAA,CACA,uDAAA,CAEF,IACE,sDAAA,CAEF,KACE,SAAA,CACA,iDAAA,CAAA,CAIJ,2BACE,GACE,SAAA,CACA,iDAAA,CAEF,KACE,SAAA,CACA,uDAAA,CAAA,CAMA,WACE,aAAA,CAEF,WACE,6BAAA,CACA,aAAA,CAIJ,WACE,8BAAA,CACA,oBAAA,CAEA,WACE,6BAAA,CAGF,WACE,2EAAA,CAIJ,WACE,aAAA,CAGF,WACE,aAAA,CAGF,WACE,kBAAA,CAKJ,yBACE,qCACE,cAAA,CAEA,mDACE,cAAA,CAGF,wDACE,cAAA,CAIJ,mCACE,WAAA,CACA,OAAA,CAGF,mCACE,UAAA,CACA,WAAA,CACA,cAAA,CAGF,mCACE,cAAA,CAGF,mCACE,cAAA,CAIA,2CACE,UAAA,CACA,WAAA,CACA,cAAA,CAGF,2CACE,aAAA,CAAA","sourcesContent":["\n.achievement-system {\n  position: relative;\n}\n\n.achievement-header {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  margin-bottom: 12px;\n  padding-bottom: 8px;\n  border-bottom: 2px solid #f39c12;\n\n  .header-icon {\n    font-size: 20px;\n  }\n\n  .header-title {\n    font-size: 16px;\n    font-weight: bold;\n    color: #2c3e50;\n  }\n\n  .achievement-count {\n    margin-left: auto;\n    font-size: 14px;\n    color: #7f8c8d;\n    background: rgba(0, 0, 0, 0.05);\n    padding: 2px 8px;\n    border-radius: 10px;\n  }\n}\n\n.achievement-list {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n  max-height: 200px;\n  overflow-y: auto;\n  padding-right: 4px;\n\n  &::-webkit-scrollbar {\n    width: 4px;\n  }\n\n  &::-webkit-scrollbar-thumb {\n    background: #bdc3c7;\n    border-radius: 2px;\n  }\n}\n\n.achievement-item {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding: 10px;\n  background: rgba(255, 255, 255, 0.5);\n  border-radius: 8px;\n  border: 2px solid #ecf0f1;\n  cursor: pointer;\n  transition: all 0.3s;\n\n  &:hover {\n    transform: translateX(5px);\n    border-color: #f39c12;\n    background: rgba(255, 255, 255, 0.8);\n  }\n\n  &.unlocked {\n    border-color: #27ae60;\n    background: linear-gradient(135deg, rgba(39, 174, 96, 0.1), rgba(46, 204, 113, 0.1));\n\n    .achievement-icon {\n      background: linear-gradient(135deg, #27ae60, #2ecc71);\n    }\n  }\n\n  &.secret {\n    border-style: dashed;\n    border-color: #9b59b6;\n\n    .achievement-icon {\n      background: linear-gradient(135deg, #9b59b6, #8e44ad);\n    }\n  }\n}\n\n.achievement-icon {\n  width: 40px;\n  height: 40px;\n  border-radius: 50%;\n  background: linear-gradient(135deg, #bdc3c7, #95a5a6);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 20px;\n  flex-shrink: 0;\n}\n\n.achievement-info {\n  flex: 1;\n  min-width: 0;\n}\n\n.achievement-name {\n  font-weight: bold;\n  font-size: 14px;\n  color: #2c3e50;\n  margin-bottom: 2px;\n}\n\n.achievement-desc {\n  font-size: 12px;\n  color: #7f8c8d;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.achievement-progress {\n  margin-top: 4px;\n  height: 6px;\n  background: #ecf0f1;\n  border-radius: 3px;\n  overflow: hidden;\n  position: relative;\n\n  .progress-bar {\n    height: 100%;\n    background: linear-gradient(90deg, #f39c12, #e67e22);\n    border-radius: 3px;\n    transition: width 0.5s ease;\n  }\n\n  .progress-text {\n    position: absolute;\n    right: 4px;\n    top: 50%;\n    transform: translateY(-50%);\n    font-size: 8px;\n    color: #7f8c8d;\n  }\n}\n\n.unlock-badge {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n\n  .badge-icon {\n    width: 24px;\n    height: 24px;\n    background: #27ae60;\n    color: white;\n    border-radius: 50%;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 14px;\n  }\n\n  .badge-time {\n    font-size: 10px;\n    color: #95a5a6;\n    margin-top: 2px;\n  }\n}\n\n.unlock-popup {\n  position: fixed;\n  top: 20px;\n  left: 50%;\n  transform: translateX(-50%);\n  z-index: 9999;\n\n  .popup-content {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    padding: 16px 24px;\n    background: linear-gradient(135deg, #f39c12, #e67e22);\n    border-radius: 12px;\n    box-shadow: 0 10px 30px rgba(243, 156, 18, 0.4);\n    color: white;\n  }\n\n  .popup-icon {\n    font-size: 36px;\n  }\n\n  .popup-title {\n    font-size: 12px;\n    opacity: 0.9;\n  }\n\n  .popup-name {\n    font-size: 18px;\n    font-weight: bold;\n  }\n}\n\n.achievement-popup-enter-active {\n  animation: popIn 0.5s ease;\n}\n\n.achievement-popup-leave-active {\n  animation: popOut 0.5s ease;\n}\n\n@keyframes popIn {\n  0% {\n    opacity: 0;\n    transform: translateX(-50%) translateY(-50px) scale(0.5);\n  }\n  50% {\n    transform: translateX(-50%) translateY(10px) scale(1.1);\n  }\n  100% {\n    opacity: 1;\n    transform: translateX(-50%) translateY(0) scale(1);\n  }\n}\n\n@keyframes popOut {\n  0% {\n    opacity: 1;\n    transform: translateX(-50%) translateY(0) scale(1);\n  }\n  100% {\n    opacity: 0;\n    transform: translateX(-50%) translateY(-30px) scale(0.8);\n  }\n}\n\n:global(.dark-mode) .achievement-system {\n  .achievement-header {\n    .header-title {\n      color: #ecf0f1;\n    }\n    .achievement-count {\n      background: rgba(255, 255, 255, 0.1);\n      color: #bdc3c7;\n    }\n  }\n\n  .achievement-item {\n    background: rgba(255, 255, 255, 0.05);\n    border-color: #566573;\n\n    &:hover {\n      background: rgba(255, 255, 255, 0.1);\n    }\n\n    &.unlocked {\n      background: linear-gradient(135deg, rgba(39, 174, 96, 0.2), rgba(46, 204, 113, 0.2));\n    }\n  }\n\n  .achievement-name {\n    color: #ecf0f1;\n  }\n\n  .achievement-desc {\n    color: #95a5a6;\n  }\n\n  .achievement-progress {\n    background: #34495e;\n  }\n}\n\n/*  */\n@media (max-width: 360px) {\n  .achievement-header {\n    flex-wrap: wrap;\n\n    .header-title {\n      font-size: 14px;\n    }\n\n    .achievement-count {\n      font-size: 12px;\n    }\n  }\n\n  .achievement-item {\n    padding: 8px;\n    gap: 8px;\n  }\n\n  .achievement-icon {\n    width: 32px;\n    height: 32px;\n    font-size: 16px;\n  }\n\n  .achievement-name {\n    font-size: 12px;\n  }\n\n  .achievement-desc {\n    font-size: 10px;\n  }\n\n  .unlock-badge {\n    .badge-icon {\n      width: 20px;\n      height: 20px;\n      font-size: 12px;\n    }\n\n    .badge-time {\n      font-size: 8px;\n    }\n  }\n}\n"],"sourceRoot":""}]);
