import { klona } from 'klona';
import { Schema } from '../../../schema';
import type { ZodIssue } from 'zod';

export function useDataExport() {
  //  JSON 
  function exportData(data: unknown, filename = '-') {
    try {
      const exportObj = {
        version: '1.0',
        exportTime: new Date().toISOString(),
        data: klona(data),
      };

      const jsonStr = JSON.stringify(exportObj, null, 2);
      const blob = new Blob([jsonStr], { type: 'application/json' });
      const url = URL.createObjectURL(blob);

      const a = document.createElement('a');
      a.href = url;
      a.download = `${filename}-${new Date().toLocaleDateString().replace(/\//g, '-')}.json`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);

      return { success: true } as const;
    } catch (e) {
      console.error('Export failed:', e);
      return { success: false, error: String(e) } as const;
    }
  }

  //  JSON 
  function importData(onSuccess: (data: ReturnType<typeof Schema.parse>) => void, onError: (error: string) => void) {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = '.json';

    input.onchange = async e => {
      const file = (e.target as HTMLInputElement).files?.[0];
      if (!file) {
        onError('');
        return;
      }

      try {
        const text = await file.text();
        const json = JSON.parse(text);

        // 
        if (!json.version || !json.data) {
          onError('');
          return;
        }

        //  zod 
        const result = Schema.safeParse(json.data);
        if (!result.success) {
          //  zod  issues
          const errorMsg = result.error.issues
            .map((issue: ZodIssue) => {
              const path = Array.isArray(issue.path) && issue.path.length > 0 ? issue.path.join('.') : '(root)';
              return `${path}: ${issue.message}`;
            })
            .join('\n');

          onError(`:\n${errorMsg}`);
          return;
        }

        onSuccess(result.data);
      } catch (e) {
        if (e instanceof SyntaxError) {
          onError('JSON : ');
        } else {
          onError(`: ${String(e)}`);
        }
      }
    };

    input.click();
  }

  return {
    exportData,
    importData,
  } as const;
}
