,4BACE,cAAA,CAGF,4BACE,gBAAA,CACA,cAAA,CACA,aAAA,CAGF,mCACE,cAAA,CACA,aAAA,CACA,iBAAA,CACA,eAAA,CACA,mBAAA,CACA,oBAAA,CACA,2BAAA,CACA,eAAA,CAGF,6BACE,cAAA,CACA,iBAAA,CACA,YAAA,CACA,OAAA,CAGF,8BACE,aAAA,CAGF,8BACE,UAAA,CACA,eAAA,CAGF,kCACE,YAAA,CACA,kBAAA,CACA,OAAA,CAGF,oCACE,cAAA,CACA,aAAA,CACA,kBAAA,CAGF,4CACE,MAAA,CACA,UAAA,CACA,kBAAA,CACA,iBAAA,CACA,eAAA,CACA,yCAAA,CAGF,kCACE,WAAA,CACA,iBAAA,CACA,6BAAA,CAGF,oCACE,cAAA,CACA,gBAAA,CACA,cAAA,CACA,gBAAA,CAGF,iCACE,YAAA,CACA,kBAAA,CACA,OAAA,CACA,cAAA,CACA,eAAA,CACA,6BAAA,CAGF,iCACE,cAAA,CAGF,kCACE,cAAA,CACA,aAAA,CACA,eAAA,CAKA,WACE,aAAA,CACA,2BAAA,CAGF,WACE,yEAAA,CACA,oBAAA,CAEA,WACE,iDAAA,CAGF,WACE,oBAAA,CACA,0CAAA,CAIJ,WACE,aAAA,CAGF,WACE,aAAA,CAGF,WACE,aAAA,CAGF,WACE,aAAA,CAGF,WACE,aAAA,CAGF,WACE,kBAAA,CAGF,WACE,wBAAA,CAGF,WACE,aAAA,CAKJ,yBACE,kCACE,yDAAA,CACA,OAAA,CAGF,iCACE,YAAA,CAGF,8BACE,iBAAA,CAGF,4BACE,cAAA,CAGF,4BACE,cAAA,CAGF,mCACE,cAAA,CACA,oBAAA,CAGF,kCACE,OAAA,CAGF,oCACE,aAAA,CAGF,oCACE,cAAA,CACA,cAAA,CAAA","sourcesContent":["\n.body-stats-block {\n  margin-bottom: 20px;\n}\n\n.block-title {\n  font-size: 18px;\n  font-weight: bold;\n  color: #c0392b;\n  margin-bottom: 12px;\n  display: inline-block;\n  border-bottom: 2px solid #c0392b;\n  transform: rotate(-2deg);\n}\n\n.body-parts-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));\n  gap: 10px;\n}\n\n.body-part-card {\n  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95), rgba(250, 250, 250, 0.9));\n  border: 2px solid #e8e8e8;\n  border-radius: 12px;\n  padding: 12px;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  position: relative;\n  overflow: hidden;\n\n  &::before {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    height: 3px;\n    background: linear-gradient(90deg, #e74c3c, #f39c12);\n    opacity: 0;\n    transition: opacity 0.3s;\n  }\n\n  &:hover {\n    transform: translateY(-3px);\n    border-color: #e74c3c;\n    box-shadow: 0 6px 20px rgba(231, 76, 60, 0.15);\n\n    &::before {\n      opacity: 1;\n    }\n  }\n\n  &:active {\n    transform: translateY(-1px);\n  }\n}\n\n.part-header {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  margin-bottom: 8px;\n}\n\n.part-icon {\n  font-size: 16px;\n}\n\n.part-name {\n  font-weight: bold;\n  font-size: 14px;\n  color: #2c3e50;\n}\n\n.part-description {\n  font-size: 12px;\n  color: #34495e;\n  margin-bottom: 6px;\n  line-height: 1.4;\n  display: -webkit-box;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  overflow: hidden;\n}\n\n.part-state {\n  font-size: 11px;\n  margin-bottom: 8px;\n  display: flex;\n  gap: 4px;\n}\n\n.state-label {\n  color: #7f8c8d;\n}\n\n.state-value {\n  color: #555;\n  font-weight: 500;\n}\n\n.sensitivity-row {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n.sensitivity-label {\n  font-size: 10px;\n  color: #7f8c8d;\n  white-space: nowrap;\n}\n\n.sensitivity-bar-container {\n  flex: 1;\n  height: 6px;\n  background: #ecf0f1;\n  border-radius: 3px;\n  overflow: hidden;\n  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);\n}\n\n.sensitivity-bar {\n  height: 100%;\n  border-radius: 3px;\n  transition: width 0.3s ease-out;\n}\n\n.sensitivity-value {\n  font-size: 11px;\n  font-weight: bold;\n  min-width: 32px;\n  text-align: right;\n}\n\n.injection-info {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  margin-top: 6px;\n  padding-top: 6px;\n  border-top: 1px dashed #e0e0e0;\n}\n\n.injection-icon {\n  font-size: 12px;\n}\n\n.injection-value {\n  font-size: 11px;\n  color: #3498db;\n  font-weight: 500;\n}\n\n// \n:global(.dark-mode) .body-stats-block {\n  .block-title {\n    color: #ff6b6b;\n    border-bottom-color: #ff6b6b;\n  }\n\n  .body-part-card {\n    background: linear-gradient(135deg, rgba(45, 52, 54, 0.95), rgba(30, 39, 46, 0.9));\n    border-color: #566573;\n\n    &::before {\n      background: linear-gradient(90deg, #ff6b6b, #feca57);\n    }\n\n    &:hover {\n      border-color: #ff6b6b;\n      box-shadow: 0 6px 20px rgba(255, 107, 107, 0.2);\n    }\n  }\n\n  .part-name {\n    color: #ecf0f1;\n  }\n\n  .part-description {\n    color: #bdc3c7;\n  }\n\n  .state-label {\n    color: #95a5a6;\n  }\n\n  .state-value {\n    color: #dfe6e9;\n  }\n\n  .sensitivity-label {\n    color: #95a5a6;\n  }\n\n  .sensitivity-bar-container {\n    background: #34495e;\n  }\n\n  .injection-info {\n    border-top-color: #566573;\n  }\n\n  .injection-value {\n    color: #5dade2;\n  }\n}\n\n/*  */\n@media (max-width: 360px) {\n  .body-parts-grid {\n    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));\n    gap: 8px;\n  }\n\n  .body-part-card {\n    padding: 10px;\n  }\n\n  .part-header {\n    margin-bottom: 6px;\n  }\n\n  .part-icon {\n    font-size: 14px;\n  }\n\n  .part-name {\n    font-size: 12px;\n  }\n\n  .part-description {\n    font-size: 11px;\n    -webkit-line-clamp: 1;\n  }\n\n  .sensitivity-row {\n    gap: 4px;\n  }\n\n  .sensitivity-label {\n    font-size: 9px;\n  }\n\n  .sensitivity-value {\n    font-size: 10px;\n    min-width: 28px;\n  }\n}\n"],"sourceRoot":""}]);
