\n  position: relative;\n}\n\n.filter-input {\n  flex: 1;\n  padding: 6px 10px;\n  border: 1px solid #bdc3c7;\n  border-radius: 15px;\n  font-size: 12px;\n  outline: none;\n  transition: all 0.3s;\n\n  &:focus {\n    border-color: #3498db;\n    box-shadow: 0 0 5px rgba(52, 152, 219, 0.3);\n  }\n}\n\n.filter-icon {\n  position: absolute;\n  right: 45px;\n  font-size: 12px;\n  opacity: 0.5;\n}\n\n.sort-btn {\n  cursor: pointer;\n  font-size: 16px;\n  padding: 4px;\n  transition: transform 0.2s;\n\n  &:hover {\n    transform: scale(1.2);\n  }\n}\n\n.inventory-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));\n  gap: 8px;\n}\n\n.item-card {\n  background: rgba(255, 255, 255, 0.8);\n  border: 2px solid #ecf0f1;\n  border-radius: 8px;\n  padding: 8px;\n  text-align: center;\n  cursor: pointer;\n  transition: all 0.3s;\n\n  &:hover {\n    transform: translateY(-3px);\n    border-color: #3498db;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);\n  }\n}\n\n.item-name {\n  font-weight: bold;\n  font-size: 12px;\n  color: #2c3e50;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.item-count {\n  font-size: 10px;\n  color: #7f8c8d;\n}\n\n.item-desc {\n  font-size: 10px;\n  color: #95a5a6;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  margin-top: 2px;\n}\n\n.empty-inventory {\n  grid-column: 1 / -1;\n  text-align: center;\n  color: #95a5a6;\n  font-style: italic;\n  padding: 20px;\n}\n\n:global(.dark-mode) .inventory-section {\n  .block-title {\n    color: #ff6b6b;\n    border-bottom-color: #ff6b6b;\n  }\n\n  .filter-input {\n    background: rgba(255, 255, 255, 0.1);\n    border-color: #566573;\n    color: #ecf0f1;\n\n    &::placeholder {\n      color: #7f8c8d;\n    }\n  }\n\n  .item-card {\n    background: rgba(255, 255, 255, 0.05);\n    border-color: #566573;\n\n    &:hover {\n      background: rgba(255, 255, 255, 0.1);\n      border-color: #5dade2;\n    }\n\n    .item-name {\n      color: #ecf0f1;\n    }\n\n    .item-count {\n      color: #bdc3c7;\n    }\n  }\n\n  .empty-inventory {\n    color: #7f8c8d;\n  }\n}\n\n/*  */\n@media (max-width: 360px) {\n  .inventory-grid {\n    grid-template-columns: repeat(auto-fill, minmax(70px, 1fr));\n    gap: 6px;\n  }\n\n  .item-card {\n    padding: 6px;\n  }\n\n  .item-name {\n    font-size: 11px;\n  }\n\n  .item-count {\n    font-size: 9px;\n  }\n\n  .item-desc {\n    font-size: 9px;\n  }\n\n  .filter-input {\n    padding: 5px 8px;\n    font-size: 11px;\n  }\n\n  .filter-icon {\n    right: 40px;\n    font-size: 11px;\n  }\n}\n"],"sourceRoot":""}]);
